import { Component, ViewChild } from "@angular/core";
import { NavController, ModalController, IonSlides } from "@ionic/angular";

import { HttpProvider } from "../../services/http";
import { SmartAudioProvider } from "../../services/smart-audio";
import { DataProvider } from "../../services/data";

import { MapPage } from "../map/map";
import { GiftsPage } from "../gifts/gifts";
import { LeadershipPage } from "../leadership/leadership";
import { BrainPage } from "../brain/brain";
import { ProfilePage } from "../profile/profile";

import { AlertsPage } from "../alerts/alerts";

import { Router } from "@angular/router";

@Component({
  selector: "page-home",
  templateUrl: "home.html"
})
export class HomePage {
  @ViewChild("slides", { static: true }) slides: IonSlides;

  readyToPlay: any;

  countries: any;
  selectedCountry: number = 0;

  randomId: number;
  newQuizzes: any;
  changeCityCost = 20;

  player = {
    role: "PLAYER",
    name: "",
    level: 0,
    avatar: null,
    xp: 0,
    gold: 0,
    silver: 0,
    trophy: 0,
    id: 0,
    code: "HOSH",
    games: 3,
    brain: {
      brainScore: {
        value: 0
      }
    }
  };

  badges = {
    quest: false,
    map: false
  };

  // for tutorial senario
  showTutorials: boolean = false;

  tutorialTips: any = [
    "Choose one of the quizzes belowðŸ‘‡ðŸ»", // games
    "Win and fill the advance bar to unlock other cities. Tap here to check the map.", // map
    "This is the performance beads. It shows your progress in each category.", // brain
    "This your avatar and level progress. by tapping it you can modify your avatar.", // profile
    "Claim your achievements and gifts here. Also submit your daily presence to collect daily gifts.", // quest
    "Here you can check the leaderboards.", // leaderboard
    "Live quizzes are special events that happens on certain dates." // live
  ];
  tutorialText: string;

  tutorialArrowPosition: any = { top: 0, left: 0 };

  constructor(
    private dataProvider: DataProvider,
    private SmartAudioProvider: SmartAudioProvider,
    public router: Router,
    public navCtrl: NavController,
    public modalCtrl: ModalController,
    private httpProvider: HttpProvider
  ) {
    this.dataProvider.needToSetIsland.subscribe((state) => {
      if (state) {
        this.setIslands(this.player.trophy);
      }
    });

    // // play home music after 4000 mili second
    // this.playAudio(1000);
    this.player = this.dataProvider.getPlayer();

    this.countries = this.dataProvider.getCountries();

    this.badges = this.dataProvider.getBadges();
    this.readyToPlay = this.dataProvider.startGame;
    this.setIslands(this.player.trophy);

    if (this.dataProvider.developerMode) {
      this.dataProvider.setPlayer({ games: 11 });
    }
  }

  async ionViewDidEnter() {
    this.SmartAudioProvider.play("change");
    // // if need to switch to any island
    if (this.dataProvider.getIslandIndex().index !== null) {
      const index = this.dataProvider.getIslandIndex().index;
      this.dataProvider.setIslandIndex(null);
      this.slides.slideTo(index, 400);
    }
    // // continue tutorial
    // if (this.player.games < 6 || this.player.games == 10) {
    //   this.handleTutorial(this.player.games, 0);
    // }
  }

  playAudio(delay: number) {
    setTimeout(() => {
      this.SmartAudioProvider.homeMusic();
    }, delay);
  }

  handleTutorial(games, delay) {
    if (games == 0 && this.readyToPlay.ready) {
      this.setTextAndArrowPositionAndShowTutorial(
        games,
        "island-image-najaf",
        -20,
        20,
        delay
      );
    }
    if (games == 1) {
      this.setTextAndArrowPositionAndShowTutorial(
        games,
        "map-box-home",
        18,
        -73,
        delay
      );
    }
    if (games == 2) {
      this.setTextAndArrowPositionAndShowTutorial(
        games,
        "brain-icon-home",
        -70,
        -7,
        delay
      );
    }
    if (games == 3) {
      this.setTextAndArrowPositionAndShowTutorial(
        games,
        "home-avatar-component",
        0,
        -45,
        delay
      );
    }
    if (games == 4) {
      this.setTextAndArrowPositionAndShowTutorial(
        games,
        "quest-icon-home",
        50,
        -8,
        delay
      );
    }
    if (games == 5) {
      this.setTextAndArrowPositionAndShowTutorial(
        games,
        "leaderboard-icon-home",
        -65,
        -8,
        delay
      );
    }
    if (games == 10) {
      this.setTextAndArrowPositionAndShowTutorial(
        games,
        "live-icon-home",
        -70,
        -7,
        delay
      );
    }
  }

  setTextAndArrowPositionAndShowTutorial(games, id, deltaY, deltaX, delay) {
    // handle tutorial with game = 10
    // set games to last item of tutorialTips
    if (games > this.tutorialTips.length - 1) {
      games = this.tutorialTips.length - 1;
    }
    // set text
    this.tutorialText = this.tutorialTips[games];

    // correction delta when using delay (when the icon transited left or right)
    // if delay > 0 then player should see the arrow onboarding with diffrent delta

    if (delay) {
      if (games == 2) {
        deltaX = deltaX + 50;
        deltaY = deltaY - 30;
      }
      if (games == 3) {
        deltaX = deltaX - 20;
        deltaY = deltaY + 150;
      }
      if (games == 4) {
        deltaX = deltaX - 50;
        deltaY = deltaY - 30;
      }
      if (games == 5) {
        deltaX = deltaX - 112;
        deltaY = deltaY - 60;
      }
      if (games == 6) {
        deltaX = deltaX + 111;
        deltaY = deltaY - 60;
      }
    }

    // set arrow position
    try {
      const element = document.getElementById(id).getBoundingClientRect();
      this.tutorialArrowPosition.top = +element.top.toFixed(0) + deltaY + "px";
      this.tutorialArrowPosition.left =
        +element.left.toFixed(0) + deltaX + "px";
    } catch (error) {
      this.tutorialArrowPosition = {
        top: "-200px",
        left: "0"
      };
    }

    // show updated tutorial state
    setTimeout(() => {
      this.checkForTutorial(id);
    }, delay);
  }

  async checkForTutorial(id: string) {
    const condition = await this.dataProvider.getFromStorage(id);

    if (!condition) {
      this.showTutorials = true;
      this.dataProvider.setTostorage(id, true);
    }
  }

  tutorialSubmit() {
    this.showTutorials = false;
  }

  setIslands(myTrophy) {
    this.countries.sort((a, b) => a.trophy - b.trophy);
    let myIsland = this.countries.find((con) => con.trophy > myTrophy);

    this.countries.map((con, index) => {
      if (con.trophy < myIsland.trophy) {
        // past island
        con["lock"] = false;
      } else if (con.trophy > myIsland.trophy) {
        // next island
        con["lock"] = true;
      } else {
        // current island
        con["lock"] = false;

        try {
          this.slides.slideTo(index, 0);
        } catch (error) {
          setTimeout(() => this.setIslands(myTrophy), 1000);
          return;
        }
      }

      con["src"] = "../../assets/imgs/islands/" + con.enName + ".png";

      con.cities.map((city) => {
        city["src"] = "../../assets/imgs/icons/" + city.info + ".png";
        city["class"] = "city-category-" + city.category;
      });
    });
  }

  slideNext() {
    this.slides.slideNext();
  }

  slidePrev() {
    this.slides.slidePrev();
  }

  async countryChanged() {
    this.SmartAudioProvider.play("slide");
    this.selectedCountry = await this.slides.getActiveIndex();

    if (this.selectedCountry > this.countries.length - 1) {
      this.slides.slideTo(this.countries.length - 1);
    }
  }

  iAmNotReadyToPlay() {
    if (!this.readyToPlay.ready) {
      return;
    }

    this.dataProvider.setStartGame(false);
    this.slides.lockSwipes(false);
  }

  async preCheck() {
    if (this.readyToPlay.ready) {
      return;
    }

    if (!this.countries[await this.slides.getActiveIndex()].lock) {
      this.SmartAudioProvider.play("buttonClick");
      // set this state in dataprovider for all page
      this.dataProvider.setStartGame(true);

      // player can not swipe in either direction on slide
      this.slides.lockSwipes(true);

      // set random city
      this.selectRandomGame();

      // show first tutorial after click on start game button
      if (this.player.games == 0) {
        this.handleTutorial(0, 0);
      }
    } else {
      // const payload = {
      //   code: 20,
      //   count:
      //     this.countries[(await this.slides.getActiveIndex()) - 1].trophy -
      //     this.player.trophy,
      // };
      // this.presentAlert(payload);
    }
  }

  async selectRandomGame(force = false) {
    const body = { force };

    this.httpProvider.post(
      "/timetrials/random",
      body,
      (res) => {
        if (res.status == "success") {
          this.dataProvider.setPlayer({ silver: res.data.silver });
        } else {
          this.slides.lockSwipes(false);
          this.dataProvider.setStartGame(false);
          this.presentAlert(res.data);
        }
      },
      (error) => {
        this.dataProvider.setStartGame(false);
        this.slides.lockSwipes(false);
        this.presentAlert({
          code: "CONNECTION_ERROR",
          reply: () => {
            this.selectRandomGame(force);
          }
        });
      }
    );

    if (!force) {
      const cities = this.countries[await this.slides.getActiveIndex()].cities;
      const randomIndex = Math.floor(Math.random() * cities.length);
      this.randomId = cities[randomIndex].id;
    } else {
      this.randomId = -1;
    }
  }

  async startGame(id) {
    this.SmartAudioProvider.play("start");

    this.countries[await this.slides.getActiveIndex()].cities.map(
      async (city) => {
        if (city.id == id) {
          // set next tutorial
          if (this.player.games < 4 || this.player.games == 9) {
            this.handleTutorial(this.player.games + 1, 5000);
          }

          const country = this.countries.find(
            (con) => con.trophy > this.player.trophy
          );

          this.dataProvider.setGame({
            city,
            country,
            countryIndex: this.countries.indexOf(country)
          });

          this.slides.lockSwipes(false);

          this.router.navigate(["/game"]);
        }
      }
    );
  }

  async goToBrain(componentProps = null) {
    if (this.readyToPlay.ready) {
      return;
    }

    this.SmartAudioProvider.play("trophy");
    let modal = await this.modalCtrl.create({
      component: BrainPage,
      componentProps
    });
    modal.present();
  }

  async goToGifts() {
    if (this.readyToPlay.ready) {
      return;
    }

    this.SmartAudioProvider.play("trophy");
    let modal = await this.modalCtrl.create({ component: GiftsPage });
    modal.present();
    modal.onDidDismiss().then((payload) => {
      if (payload.data == "myQmarz") {
        this.goToProfile({ activeTab: 1 });
      }
    });
  }

  async goToMap() {
    if (this.readyToPlay.ready) {
      return;
    }

    this.SmartAudioProvider.play("trophy");

    let mapModal = await this.modalCtrl.create({
      component: MapPage
    });
    mapModal.present();

    mapModal.onDidDismiss().then(async (payload) => {
      if (payload.data) {
        let duration =
          Math.abs(
            (await this.slides.getActiveIndex()) - payload.data["setIsland"]
          ) * 400;
        this.slides.slideTo(payload.data["setIsland"], duration);
      }
    });
  }

  async goToLeaderboard() {
    if (this.readyToPlay.ready) {
      return;
    }
    this.SmartAudioProvider.play("trophy");

    const leaderboardModal = await this.modalCtrl.create({
      component: LeadershipPage
    });
    leaderboardModal.present();
  }

  async goToProfile(componentProps = null) {
    if (this.readyToPlay.ready) {
      return;
    }

    this.SmartAudioProvider.play("trophy");
    let profileModal = await this.modalCtrl.create({
      component: ProfilePage,
      componentProps: { isForMe: true, ...componentProps }
    });
    profileModal.present();
  }

  async presentAlert(payload) {
    if (!this.dataProvider.isShowingAlert.value) {
      let prizeModal = await this.modalCtrl.create({
        component: AlertsPage,
        componentProps: { payload }
      });
      prizeModal.present();
    }
  }
}
